import React, {useEffect, useState} from "react"
import {useNavigate } from 'react-router-dom';
import TextField from '@mui/material/TextField';
import Select from '@mui/material/Select';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Button from "@mui/material/Button";
import Axios from "axios";



function CreateGroup(props) {
    const currentMember = props.currentMember;

    const [groupName, setGroupName] = React.useState("");
    const [groupType, setGroupType] = React.useState('');

    const handleChange = (event) => {
        setGroupType(event.target.value);
    };

    let navigate = useNavigate();
    const routeChange = (path) => {
        navigate(path);
    }

    const handleSubmit = (event) => {
        event.preventDefault()
        // console.log(image)
        const group = {
           groupName: groupName,
           groupType: groupType
        }
        return Axios.post(process.env.REACT_APP_MAIN_DOMAIN + '/create-group', 
        group, 
        { withCredentials: true }
        )
        .then(function (response) {
            console.log(response)
            routeChange("/group/" + response.data.group_URL)
          })
    }

    return (
        <div>
        {currentMember? 
      <div className="create-group-wrapper">
        <h1>Create a Langroop</h1>
            <form className="create-group-form" onSubmit={handleSubmit}>
            <div className="create-group-form-entry">
                <TextField 
                    fullWidth
                    required
                    id="group-name"
                    label="Group Name"
                    placeholder="ex. Japanese Study Group"
                    value = {groupName}
                    onChange = {(e)=>{setGroupName(e.target.value)}}
                />
                </div>
                <div className="create-group-form-entry">
                <FormControl fullWidth>
                    <InputLabel id="group-type-select-label">Group Type</InputLabel>
                    <Select
                        labelId="group-type-select-label"
                        required
                        id="group-type"
                        value={groupType}
                        label="Group Type"
                        onChange={handleChange}
                    >
                        <MenuItem value={"Language Exchange"}>Language Exchange</MenuItem>
                        <MenuItem value={"Study Group"}>Study Group</MenuItem>
                        <MenuItem value={"Class"}>Class</MenuItem>
                    </Select>
                </FormControl>
                </div>
                <Button
                    variant="contained"
                    type="submit"
                >Submit</Button>
            </form>
        </div>
        :
        <h1>Open a member profile to create a group!</h1>
        }
        </div>
    )
}

export default CreateGroup
