import * as React from 'react';
import TextField from '@mui/material/TextField';
import languageList from './languages';
import MyCropper from "./cropper"

import { useNavigate } from 'react-router-dom';

import { useTheme } from '@mui/material/styles';
import Box from '@mui/material/Box';
import OutlinedInput from '@mui/material/OutlinedInput';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import Chip from '@mui/material/Chip';
import FormHelperText from '@mui/material/FormHelperText';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import PhotoCamera from '@mui/icons-material/PhotoCamera';
import { styled } from '@mui/material/styles';
import { CloseOutlined, Image } from '@mui/icons-material';
import Axios from 'axios';
import ImageUpload from '../newCropper/ImageUpload';
import Alert from '@mui/material/Alert';


const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
    PaperProps: {
        style: {
            maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
            width: 250,
        },
    },
};

// function getStyles(languageList.label, language, theme) {
//     return {
//       fontWeight:
//        langauge.indexOf(name) === -1
//           ? theme.typography.fontWeightRegular
//           : theme.typography.fontWeightMedium,
//     };
//   }

function Profile(props) {
    const currentMember = props.currentMember

    // Axios.get(process.env.REACT_APP_MAIN_DOMAIN + '/profile', { withCredentials: true })
    //     .then(function (response) {
    //         console.log(response)
    //     })


    const theme = useTheme();
    const [firstName, setFirstName] = React.useState("");
    const [lastName, setLastName] = React.useState("");
    const [nativeLanguage, setNativeLanguage] = React.useState("");
    const [fluentLanguages, setFluentLanguages] = React.useState([]);
    const [learningLanguages, setLearningLanguages] = React.useState([]);
    const [image, setImage] = React.useState("../images/blank-profile.jpg");
    const [cropper, setCropper] = React.useState(false);
    const [alert, setAlert] = React.useState(false);
    const [status, setStatus] = React.useState("")

    let navigate = useNavigate();
    const routeChange = (path) => {
        navigate(path);
    }

    const handleChange = (event) => {
        const {
            target: { value },
        } = event;
        if (event.target.name === "fluentLanguages") {
            setFluentLanguages(
                // On autofill we get a stringified value.
                typeof value === 'string' ? value.split(',') : value,
            );
        } else {
            setLearningLanguages(
                // On autofill we get a stringified value.
                typeof value === 'string' ? value.split(',') : value,
            );
        }
    };

    async function handleSubmit(event) {
        event.preventDefault()
        // console.log(image)
        if (image === "../images/blank-profile.jpg") {
           setAlert(true);
        } else {
            console.log("click")
            const userProfile = {
                firstName: firstName,
                lastName: lastName,
                profilePic: image,
                nativeLanguage: nativeLanguage,
                fluentLanguages: fluentLanguages,
                learningLanguages: learningLanguages,
            }
            const createProfile = await Axios.post(process.env.REACT_APP_MAIN_DOMAIN + '/profile', userProfile, { withCredentials: true })
                .then(function (response) {
                    console.log(response.data)
                    // props.setCurrentMember()
                    props.getCurrentMember()
                    routeChange("/")
                })
                .catch((err) => {
                    console.log(err)
                })
        }
    }

    return (
        <div >
            {currentMember ?
                routeChange("/member/" + currentMember._id)
                :
                <div className='profile-wrapper'>
                    <h1>Set up your member profile!</h1>
                    <form onSubmit={handleSubmit}>
                        <div className="profile-name-entry">
                            <div className="profile-entry">
                                <TextField
                                    required
                                    id="outlined-required"
                                    label="First Name"
                                    placeholder="First Name"
                                    value={firstName}
                                    onChange={(e) => { setFirstName(e.target.value) }}
                                />
                            </div>
                            <div className="profile-entry">
                                <TextField
                                    required
                                    id="outlined-required"
                                    label="Last Name"
                                    placeholder="Last Name"
                                    value={lastName}
                                    onChange={(e) => { setLastName(e.target.value) }}
                                />
                            </div>
                        </div>
                        {/*---------------------- PROFILE PIC---------------------------- */}
                        <div className="profile-pic-section">
                            <div className='profile-pic-display-wrapper'>
                                <img className="profile-pic-display" src={image} ></img>
                                <Button className="profile-pic-display-button" component="span" onClick={() => { setCropper(true); setAlert(false) }}>
                                    <PhotoCamera />
                                </Button>
                            </div>
                            <p>select a profile picture</p>
                            {alert && <Alert severity="error">Please upload a profile picture.</Alert>}
                        </div>
                        {/*---------------------- NATIVE LANGUAGE---------------------------- */}
                        <div className="native-language-entry">
                            <FormControl fullWidth required>
                                <InputLabel id="native-language-selector-label">Native Language</InputLabel>
                                <Select

                                    labelId="native-language-selector-label"
                                    id="native-language-selector"
                                    value={nativeLanguage}
                                    label="Native Language"
                                    onChange={(event) => { setNativeLanguage(event.target.value) }}
                                >
                                    {languageList.map((language) => (
                                        <MenuItem
                                            key={language.value}
                                            value={language.value}
                                        //   style={getStyles(language.label, language, theme)}
                                        >
                                            {language.label}
                                        </MenuItem>
                                    ))}
                                </Select>
                            </FormControl>
                        </div>
                        <div className="profile-language-entry">
                            {/*---------------------- FLUENT LANGUAGES---------------------------- */}
                            <div>
                                <FormControl sx={{ m: 1, width: 300 }}>
                                    <InputLabel id="fluent-languages-selector-label">Fluent Languages</InputLabel>
                                    <Select
                                        labelId="fluent-languages-selector-label"
                                        id="fluent-languages-selector"
                                        multiple
                                        name="fluentLanguages"
                                        value={fluentLanguages}
                                        onChange={handleChange}
                                        input={<OutlinedInput id="fluent-languages-selector" label="Fluent Languages" />}
                                        renderValue={(selectedFluent) => (
                                            <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
                                                {selectedFluent.map((value) => (
                                                    <Chip key={value} label={value} />
                                                ))}
                                            </Box>
                                        )}
                                        MenuProps={MenuProps}
                                    >
                                        {languageList.map((language) => (
                                            <MenuItem
                                                key={language.value}
                                                value={language.value}
                                            //   style={getStyles(language.label, language, theme)}
                                            >
                                                {language.label}
                                            </MenuItem>
                                        ))}
                                    </Select>
                                    <FormHelperText>(languages you are like super good at speaking)</FormHelperText>
                                </FormControl>
                            </div>
                            {/*---------------------- LEARNING LANGUAGES---------------------------- */}
                            <div>
                                <FormControl sx={{ m: 1, width: 300 }} required>
                                    <InputLabel id="learning-languages-selector-label">Learning Languages</InputLabel>
                                    <Select
                                        labelId="learning-languages-selector-label"
                                        id="learning-languages-selector"
                                        multiple
                                        name="learningLanguages"
                                        value={learningLanguages}
                                        onChange={handleChange}
                                        input={<OutlinedInput id="learning-languages-selector" label="Learning Languages" />}
                                        renderValue={(selectedLearning) => (
                                            <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
                                                {selectedLearning.map((value) => (
                                                    <Chip key={value} label={value} />
                                                ))}
                                            </Box>
                                        )}
                                        MenuProps={MenuProps}
                                    >
                                        {languageList.map((language) => (
                                            <MenuItem
                                                key={language.value}
                                                value={language.value}
                                            //   style={getStyles(language.label, language, theme)}
                                            >
                                                {language.label}
                                            </MenuItem>
                                        ))}
                                    </Select>
                                    <FormHelperText>(languages you are learning)</FormHelperText>
                                </FormControl>
                            </div>
                        </div>
                        {/*---------------------- PROFILE PICTURE UPLOAD---------------------------- */}
                        <Button
                            variant="contained"
                            type="submit"
                        >Submit</Button>
                    </form>
                    {cropper && <ImageUpload
                        setImage={setImage}
                        setCropper={setCropper}
                        width="480"
                        height="480"
                    />}
                </div>
            }
        </div>
    )
}

export default Profile