import * as React from 'react';
import Axios from "axios";

import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';

import IconButton from '@mui/material/IconButton';
import MoreHorizIcon from '@mui/icons-material/MoreHoriz';

export default function ChatPopupMenu(props) {
const currentMember = props.currentMember
const sender = props.senderId
const currentChannel = props.channel

const [senderPermissions, setSenderPermissions] = React.useState(false)

    const [anchorEl, setAnchorEl] = React.useState(null);
    const open = Boolean(anchorEl);
    const handleClick = (event) => {
    if(currentMember._id === sender|| currentMember._id === currentChannel.admin){
        setSenderPermissions(true)
    }
        setAnchorEl(event.currentTarget);
    };
    const handleClose = () => {
        setAnchorEl(null);
    };

function deleteMessage(){
    handleClose()
    const chatId = props.chat._id
    Axios.post(process.env.REACT_APP_MAIN_DOMAIN + '/chat/delete', {chatId})
    .then((response)=>{
        console.log(response)
    })
}

    return (
        <div style={{ marginLeft: "auto" }}>
            <IconButton aria-label="chat-popup-menu"
                size="small"
                sx={{ maxHeight: "20px", position: "relative", bottom: "10px", left: "5px" }}
                //   ref={anchorRef}
                id="chat-popup-menu-button"
                aria-controls={open ? "chat-popup-menu" : undefined}
                aria-expanded={open ? 'true' : undefined}
                aria-haspopup="true"
                onClick={handleClick}
            >
                <MoreHorizIcon fontSize="small" />
            </IconButton>
            <Menu
                id="chat-popup-menu"
                aria-labelledby="chat-popup-menu"
                anchorEl={anchorEl}
                open={open}
                onClose={handleClose}
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'right',
                }}
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                }}
            >
                {senderPermissions && <MenuItem onClick={deleteMessage}>Delete</MenuItem>}
                <MenuItem onClick={handleClose}>Reply</MenuItem>
                <MenuItem onClick={handleClose}>Report</MenuItem>
            </Menu>
        </div>
    );
}