import React from "react"

export default function MemberPage(props) {
const currentMember = props.currentMember

    return (
        <div>
        {currentMember? 
        <div className="member-page-wrapper">
            <div className="member-page-sidebar">
            Sidebar
            <div className="member-page-sidebar-element">Element</div>
            <div className="member-page-sidebar-element">Element</div>
            </div>
            <div className="member-page-main">
Main
            </div>
        </div>
        :
        <h2>Finish setting up your member profile to view other members!</h2>
        }
        </div>
    )
}