//all members page

import React, { useState, useEffect } from "react"
import Axios from "axios"
import MemberCard from "./memberCard"




export default function Members() {
    const [members, setMembers] = useState([])

    async function loadMembers(mounted) {
        // console.log(members)
        const query = {
            filters: {},
            limit: 10,
            sort: ""
        }
        const memberData = await Axios.post(process.env.REACT_APP_MAIN_DOMAIN + '/members/fetch', query)
            .then(function (response) {
                if (mounted) {
                    setMembers(response.data)
                }
            })
    }

    useEffect(() => {
        let mounted = true;
        loadMembers(mounted)
        return () => { mounted = false };
    }, [])

    return (
        <div className="members-wrapper">
        <h1>Members</h1>
        <div className="member-cards-container">
            {members && members.map((member) => (
                <MemberCard
                    key={member._id}
                    memberName={member.first_name}
                    memberNativeLanguage={member.native_language}
                    memberPic={member.profile_pic}
                    memberURL={member._id}
                />
            )
            )}
            </div>
        </div>
    )
}