import React, { useEffect, useState, useRef } from "react";
import Axios from "axios";
import Avatar from '@mui/material/Avatar';
import SendRoundedIcon from '@mui/icons-material/SendRounded';
import Fab from '@mui/material/Fab';
import Paper from '@mui/material/Paper';
import ChatPopupMenu from "./chatPopupMenu";
import socketIOClient from "socket.io-client";




function ChatLog(props) {
    const currentMember = props.currentMember
    const [typingMember,setTypingMember] = useState();
    const [chatText, setChatText] = useState("")
    const [members, setMembers] = useState([]); //full member profile
    const [chats, setChats] = useState([]); //all chats for currentChannel
    const [socketResponse, setSocketResponse] = useState("");
    const ENDPOINT = process.env.REACT_APP_SOCKET_DOMAIN 

    const socket = socketIOClient(ENDPOINT);
    socket.on('message',(message)=>{
        console.log(message)
        loadChats()
    })
    socket.on('typing',(memberName)=>{
        setTimeout(() => {
            setTypingMember()
        }, 3000);
        setTypingMember(memberName)
    })

    // socket.on('online', (member)=>{
    //     // console.log(props.online)
    //     if(props.online){
    //     if(props.online.includes(member)){

    //     }
    //     else{
            
    //       props.setOnline([member, ...props.online])  
    //     }
    // }
    // else{
    //     props.setOnline([member])
    // }
    // })
    

    useEffect(() => {
        console.log("channel change")
        socket.emit('channel', props.channel._id, currentMember)
    }, [props.channel]);


    const messagesEndRef = useRef(null);

    const scrollToBottom = () => {
        messagesEndRef.current?.scrollIntoView({ behavior: "smooth" })
    }

    useEffect(() => {
        scrollToBottom()
    }, [chats])

    async function getChannelMembers() { //get Ids of people subscribed to channel
        const channel = {
            channel_id: props.channelId,
            channel_type: props.channelType
        }
        const channelMembers = await Axios.post(process.env.REACT_APP_MAIN_DOMAIN + '/channels/members/fetch', channel)
            .then(function (response) {
                console.log(response.data)
                setMembers(response.data)
            })
    }
    useEffect(() => {
        console.log("effect")
        getChannelMembers()
    }, [props.channelId])

    async function loadChats() {
        const currentChannel = props.channelId
        const getChats = await Axios.post(process.env.REACT_APP_MAIN_DOMAIN + '/chats/fetch', { currentChannel })
            .then(function (response) {
                // console.log(response.data)
                setChats(response.data)
            })
    }

    useEffect(() => {
        loadChats()
    }, [props.channelId])

    function sendMessage(e) {
        // e.preventDefault()
        const channel = props.channelId
        Axios.post(process.env.REACT_APP_MAIN_DOMAIN + '/chats/post', { chatText, channel }, { withCredentials: true })
            .then(function (response) {
                console.log(response)
                setChatText("")
                loadChats()
                const socket = socketIOClient(ENDPOINT);
                socket.emit('message', props.channel._id)

            })
    }

    function getSender(id, resultKey) {
        const sender = members.find(m => m._id === id)
        if (!sender) {
        } else {
            return sender[resultKey]
        }
    }

    return (
        <div className="chats-wrapper">
            <div>{chats && chats.map((c) =>
                <div key={c._id}>
                    <div className="chat-block"
                        style={{ flexDirection: (c.sender_id === props.currentMember._id) && "row-reverse" }}
                    >
                        {/*  */}
                        <Avatar className="chat-avatar" alt="null"
                            src={members && getSender(c.sender_id, "profile_pic")}></Avatar>
                        {/* <Paper elevation={2}> */}
                        <div className="chat-message-wrapper"
                            style={{ backgroundColor: (c.sender_id === props.currentMember._id) && "#4ad5ff" }}
                        >
                            <div style={{ display: "flex", flexDirection: "row" }}>
                                <p className="chat-sender-name">{members && getSender(c.sender_id, "first_name")}</p>
                                <ChatPopupMenu chat={c} senderId={c.sender_id} channel={props.channel} currentMember={props.currentMember} />
                            </div>
                            {(c.status === "deleted") ? <em>Deleted</em> : <p className="chat-message">{c.content}</p>}
                        </div>
                        {/* </Paper> */}
                        <div className="chat-time-wrapper">
                            <p className="chat-time">{new Date(c.created_date).toLocaleString()}</p>
                        </div>
                    </div>
                </div>)}
            </div>
            <div ref={messagesEndRef}></div>
            <div className="chat-bottom-section">
                {typingMember && <p className="chat-typing-notice">{typingMember} is typing...</p>}
                <div className="chat-input-wrapper">
                    <input className="chat-input"
                        type="text"
                        placeholder="write your message here"
                        value={chatText}
                        onChange={(e) => { setChatText(e.target.value); 
                        socket.emit('typing', currentMember.first_name ,props.channel._id)
                        }}
                        onKeyDown={(e) => { if (e.key === "Enter") { return sendMessage() } }}
                    ></input>

                    <Fab className="chat-button"
                        size="medium"
                        variant="extended"
                        color="primary"
                        // type="submit"
                        onClick={sendMessage}
                    >
                        <SendRoundedIcon /> Send
                    </Fab>
                </div>
            </div>
        </div>
    )
}

export default ChatLog