import React, { useEffect, useState } from "react"
import { useParams } from "react-router-dom"
import Axios from "axios";
import Avatar from '@mui/material/Avatar';
import AvatarGroup from '@mui/material/AvatarGroup';
import TabsUnstyled from '@mui/base/TabsUnstyled';
import TabsListUnstyled from '@mui/base/TabsListUnstyled';
import TabPanelUnstyled from '@mui/base/TabPanelUnstyled';
import TabUnstyled from '@mui/base/TabUnstyled';

function EventPage(props) {
    const { eventURL } = useParams();
    const [event, setEvent] = useState();
    const [attendeesId, setAttendeesId] = useState([]);
    const [members, setMembers] = useState([]); //full member profile


    async function loadEventData() {
        const eventData = await Axios.get(process.env.REACT_APP_MAIN_DOMAIN + '/event/' + eventURL)
            .then(function (response) {
                setEvent(response.data)
                setAttendeesId(response.data.attendees)
                return console.log("Data Loaded")
            })
    }

    async function loadMemberData() {
        // console.log(members)
        const memberData = await Axios.post(process.env.REACT_APP_MAIN_DOMAIN + '/event/attendees/fetch', attendeesId)
            .then(function (response) {
                setMembers(response.data)
            })
    }

    // async function loadGroupChats(){
    //  const groupChannel = group.channel
    //  const groupChats = await Axios.post(process.env.REACT_APP_MAIN_DOMAIN + '/chats/fetch', groupChannel)
    //  .then(function (response) {
    //      setChat(response.data)
    //  })
    // }

    useEffect(() => {
        loadEventData()
    }, []);

    useEffect(() => {
        loadMemberData()
    }, [attendeesId]);


    function RSVP() {
        Axios.get(process.env.REACT_APP_MAIN_DOMAIN + '/event/' + eventURL + '/rsvp', { withCredentials: true })
            .then(function (response) {
                console.log(response)
                loadEventData()
            })
    }

    function cancelRSVP() {
        Axios.get(process.env.REACT_APP_MAIN_DOMAIN + '/event/' + eventURL + '/cancel-rsvp', { withCredentials: true })
            .then(function (response) {
                console.log(response)
                loadEventData()
            })
    }

    function getCurrentMember(id, resultKey) {
        const currentMember = members.find(m => m._id === id)
        if (!currentMember) {

        } else {
            return currentMember[resultKey]
        }
    }


    return (
        <div>
            <h1>Event Page</h1>
            <h2>{eventURL}</h2>
            <div className="event-page-wrapper">
                <div className="event-page-sidebar">
                    <p>Sidebar</p>
                    <div className="event-page-sidebar-element">Element</div>
                    <div className="event-page-sidebar-element">
                        <h3>Attendees</h3>
                        <AvatarGroup max={4}>
                            {members && members.map((member) => <Avatar key={member._id} alt={member.first_name} src={member.profile_pic} />)}
                        </AvatarGroup>
                    </div>
                </div>
                <div className="group-page-main">
                    <p>Main</p>
                </div>
            </div>
            {/* {members && membersId.map((memberId) => <p key={memberId}>{memberId}</p>)} */}
            <button onClick={RSVP}>RSVP</button>
            <button onClick={cancelRSVP}>Cancel</button>
        </div>
    )
}

export default EventPage