import React from "react"
import Axios from "axios"
import EventCard from "./eventCard"

function Events(){
    const options = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' };
    const [events, setEvents] = React.useState([])


    const data = Axios.get(process.env.REACT_APP_MAIN_DOMAIN + '/events')
            .then(function (response) {
                setEvents(response.data)
                // console.log(new Date(events[0].eventStartDate))
            })
    
    return (
        <div className="events-wrapper">
        <h1>Events</h1>
        <div className="event-cards-container">
            {events.map((event) =>(
                <EventCard
                    key={event._id}
                    eventName={event.event_name}
                    eventStartDate={new Date(event.event_start_date).toLocaleDateString(undefined, options)}
                    eventStartTime={new Date(event.event_start_date).toLocaleTimeString()}
                    eventPic={"https://static.wixstatic.com/media/fe6318_d455651651e04bc588b451612b4c663a~mv2.png/v1/crop/x_896,y_0,w_2022,h_3840/fill/w_339,h_638,al_c,q_85,usm_0.66_1.00_0.01/Bonjour%20(3).webp"}
                    eventURL={event.event_URL}
                />
            )
            )}
            </div>
        </div>
    )
}
export default Events