// ImageUpload.js

import React, { useState } from 'react'
//import * as firebase from 'firebase/app'
import ImageCropper from './ImageCropper'
import Axios from 'axios'
import PhotoCamera from '@mui/icons-material/PhotoCamera';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import CloseIcon from '@mui/icons-material/Close';



function ImageUpload(props){
    
    const [blob, setBlob] = useState(null)
    const [inputImg, setInputImg] = useState('')

    const getBlob = (blob) => {
        // pass blob up from the ImageCropper component
        setBlob(blob)
    }

    const onInputChange = (e) => {
        // convert image file to base64 string
        const file = e.target.files[0]
        const reader = new FileReader()

        reader.addEventListener('load', () => {
            setInputImg(reader.result)
        }, false)

        if (file) {
            reader.readAsDataURL(file)
        }
    }

    const handleSubmitImage = (event) => {
        event.preventDefault()
        console.log(blob)
        const profileFormData = new FormData();
        profileFormData.append("image", blob)
        // profileFormData.append("nativeLanguage", nativeLanguage)
        return Axios.post(process.env.REACT_APP_MAIN_DOMAIN + '/cropper', profileFormData, {
            headers: {
                'content-type': 'multipart/form-data'
            }
        }).then(function (response) {
            console.log(response.data.result.url);
            props.setImage(response.data.result.url)
          })
    }


    return (
        <div className='cropper-wrapper'>
        <Box component="form" onSubmit={handleSubmitImage}>
        {/* <form onSubmit={handleSubmitImage}> */}
            <label htmlFor="profile-pic-upload">
                    <input id="profile-pic-upload" type="file"  accept='image/*' onChange={onInputChange}></input>
                    <Button variant="contained" component="span">
                        <PhotoCamera /> <span style={{ marginLeft: "7px" }}>Choose Photo</span>
                    </Button>
                </label>
            <CloseIcon type= "button" className = "cropper-close-icon" onClick={() =>{props.setCropper(false)}}></CloseIcon>
            {/* <input
                type='file'
                accept='image/*'
                onChange={onInputChange}
            /> */}
                    <ImageCropper
                        getBlob={getBlob}
                        inputImg={inputImg}
                        width={props.width}
                        height={props.height}
                    />
            {
                inputImg && (
                    <Button
                    variant="contained"
                    type="submit">
                    Upload
                    </Button>
                    
                )
            }
        {/* </form> */}
        </Box>
        </div>
    )
}

export default ImageUpload