import React from "react"
import {useState} from "react"
import Login from "./login";
import Register from "./register";

function Authentication({setAccessCookie}){
    const [login, setLogin] = useState(false);

    return(
    <div>
    {login? <Login setLogin={setLogin} setAccessCookie={setAccessCookie}/>: 
    <Register setLogin={setLogin} setAccessCookie={setAccessCookie}/> }
    </div>
    )
}

export default Authentication