import * as React from 'react';
import Axios from "axios";

import Box from '@mui/material/Box';
import List from '@mui/material/List';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import Divider from '@mui/material/Divider';
import InboxIcon from '@mui/icons-material/Inbox';
import DraftsIcon from '@mui/icons-material/Drafts';
import Avatar from '@mui/material/Avatar';

export default function ContactsList(props) {
    const currentMember = props.currentMember


    const [selectedChannel, setSelectedChannel] = React.useState(1);
    // const [groupChats, setGroupChats] = React.useState([]);
    const [groupChannels, setGroupChannels] = React.useState();
    const [groups, setGroups] = React.useState();
    const [eventChannels, setEventChannels] = React.useState([]);
    const [events, setEvents] = React.useState();
    const [privateChats, setPrivateChats] = React.useState([]);

    async function getContacts() {
        const getMember = await Axios.get(process.env.REACT_APP_MAIN_DOMAIN + '/member/fetch', { withCredentials: true })
            .then((response) => {
                console.log(response.data)
                getGroupChannels(response.data.group_chats)
                getEventChannels(response.data.event_chats)
                // props.setCurrentMember(response.data)
            })
    }

  
    async function getGroupChannels(channelIds){
        // console.log(channelIds)
        const channels = await Axios.post(process.env.REACT_APP_MAIN_DOMAIN + '/channels/fetch',{channelIds},{ withCredentials: true })
        .then((response) => {
            // console.log(response.data)
            setGroupChannels(response.data)
            getGroups()
        })
    }

    async function getGroups(){
        console.log(groupChannels)
        const query = {
        filters: {$in: {channel: [groupChannels]}},
        limit: 10,
        sort: ""
        }
        const fetchedGroups = await Axios.post(process.env.REACT_APP_MAIN_DOMAIN + '/groups/fetch', query)
        .then((response)=>{
            // console.log(response.data)
            setGroups(response.data)
        })
    }

    

    async function getEventChannels(channelIds){
        console.log(channelIds)
        const channels = await Axios.post(process.env.REACT_APP_MAIN_DOMAIN + '/channels/fetch',{channelIds},{ withCredentials: true })
        .then((response) => {
            console.log(response.data)
            setEventChannels(response.data)
            getEvents()
        })
    }

    async function getEvents(){
        const query = {
        filters: {$in: {channel: [eventChannels]}},
        limit: 10,
        sort: ""
        }
        const fetchedEvents = await Axios.post(process.env.REACT_APP_MAIN_DOMAIN + '/events/fetch', query)
        .then((response)=>{
            console.log(response.data)
            setEvents(response.data)
        })
    }

    function getChannelPic(id, channelType, key) {
        // console.log(channelType)
        const channelOrigin = channelType.find(c => c.channel === id)
        if (!channelOrigin) {
        } else {
            return channelOrigin[key]
        }
    }

    React.useEffect(() => {
        getContacts()
    }, [])


    const handleListItemClick = (event, channel, channelType, key) => {
        setSelectedChannel(channel._id);
        props.setChannel(channel)
        props.setChannelId(channel._id)
        props.setChannelType(channel.channel_type)
        props.setChannelPic(getChannelPic(channel._id, channelType, key))
        props.setChannelName(channel.channel_name)
    };

    return (
        <div className= "contacts-list">
        <Box sx={{ boxShadow: 2, padding: "10px", overflowY: "scroll", height: "100%"}}>
        <p>Group Chats</p>
            <List component="nav" aria-label="channels">
            {groupChannels && groupChannels.map((channel)=> 
                <ListItemButton
                    key={channel._id}
                    selected={selectedChannel === channel._id}
                    onClick={(event) => handleListItemClick(event, channel, groups, "group_pic")}
                >
                    <ListItemIcon>
                    {groups && <Avatar alt={channel.channel_name} src={getChannelPic(channel._id, groups, "group_pic")} />}
                    </ListItemIcon>
                    <ListItemText primary={channel.channel_name} />
                </ListItemButton>
                )}
            </List>
            <Divider />
            <p>Event Chats</p>
            <List component="nav" aria-label="channels">
            {eventChannels && eventChannels.map((channel)=> 
                <ListItemButton
                    key={channel._id}
                    selected={selectedChannel === channel._id}
                    onClick={(event) => handleListItemClick(event, channel, events, "event_pic")}
                >
                    <ListItemIcon>
                    {events && <Avatar alt={channel.channel_name} src={getChannelPic(channel._id, events, "event_pic")} />}
                    </ListItemIcon>
                    <ListItemText primary={channel.channel_name} />
                </ListItemButton>
                )}
            </List>
            <Divider />
            <p>Private Messages</p>
            </Box>
           </div>
    );
}