import React, { useState, useEffect } from "react"
import Axios from "axios"
import GroupCard from "./groupCard"

function Groups() {
    const [groups, setGroups] = useState([])

    async function loadGroups() {
        const data = await Axios.get(process.env.REACT_APP_MAIN_DOMAIN + '/groups')
            .then(function (response) {
                setGroups(response.data)
                console.log(groups)
            })
    }

    useEffect(() => {
        loadGroups()
    }, [])

    return (
        <div className="groups-wrapper">
            <div className="group-cards-container">
                {groups && groups.map((group) => (
                    <GroupCard
                        key={group._id}
                        groupName={group.group_name}
                        groupType={group.group_type}
                        groupPic={"https://static.wixstatic.com/media/fe6318_d455651651e04bc588b451612b4c663a~mv2.png/v1/crop/x_896,y_0,w_2022,h_3840/fill/w_339,h_638,al_c,q_85,usm_0.66_1.00_0.01/Bonjour%20(3).webp"}
                        groupURL={group.group_URL}
                    />
                )
                )}
            </div>
        </div>
    )
}

export default Groups
