
import './App.css';
import Dashboard from './components/dashboard';
import Register from './components/register';
import CreateGroup from './components/createGroup';
import Authentication from "./components/authentication"
import Groups from "./components/groups"
import GroupPage from './components/groupPage';
import Meeting from './components/meeting';
import Chats from './components/chats'
import MemberPage from "./components/memberPage"
import Members from "./components/members"
import EventPage from "./components/eventPage"
import Home from "./components/home"

import { BrowserRouter, Route, Routes } from 'react-router-dom';
import Axios from "axios";

import Login from './components/login';
import useToken from './components/useToken';
import Navbar from './components/navbar';
import { useEffect, useState } from 'react';
import Cookies from "js-cookie"
import Panel from './components/panel';
import Test from "./components/test"
import Events from './components/events';
import Profile from './components/profile';
import ImageUpload from './newCropper/ImageUpload';
import BookEvent from './components/bookEvent';


function App() {
  const [accessCookie, setAccessCookie] = useState(Cookies.get("access-token"))
  const [currentMember, setCurrentMember] = useState()
  //ADD back if need for tokens
  // const {token, setToken} = useToken();

  async function getCurrentMember() {
    const currentMember = await Axios.get(process.env.REACT_APP_MAIN_DOMAIN + '/member/fetch', { withCredentials: true })
      .then((response) => {
        console.log(response.data)
        setCurrentMember(response.data)
      })
  }

  useEffect(() => {
    return getCurrentMember()
  }, [accessCookie])

  // useEffect(()=>{
  //   window.location.reload(false);
  // },[setCurrentMember])

  if (!accessCookie) {
    return <Authentication 
    setAccessCookie={setAccessCookie} 
    getCurrentMember={getCurrentMember}
    />
  }

  return (
    <div className="App">
      {/* <Navbar />
      <Panel /> */}
      <div className="wrapper">
        <BrowserRouter>
          <Routes>
            <Route path="/" element={<Panel 
              currentMember={currentMember} 
            />}>
   {/* sub paths */}
              <Route path="/" element={<Home 
               currentMember={currentMember}
              />} />

              <Route path="test" element={<Test />} />
              <Route path="events" element={<Events />} />
              <Route path="event/:eventURL" element={<EventPage />} />

              <Route path="profile" element={<Profile 
                  currentMember={currentMember}
                  getCurrentMember={getCurrentMember}
              />}/>

              <Route path="create-group"
                element={<CreateGroup
                  currentMember={currentMember}
                />} />

              <Route path="groups" element={<Groups />} />
              <Route path="group/:groupURL" element={<GroupPage />} />

              <Route path="chats"
                element={<Chats
                  currentMember={currentMember}
                />} />

              <Route path="book-event"
                element={<BookEvent
                  currentMember={currentMember}
                />} />

              <Route path="members"
                element={<Members
                  currentMember={currentMember}
                />} />

              <Route path="member/:memberURL"
                element={<MemberPage
                  currentMember={currentMember}
                />} />

              <Route path="meeting" element={<Meeting />} />
            </Route>

            <Route path="/login"
              element={<Login setAccessCookie={setAccessCookie} />}>
            </Route>
            <Route path="/dashboard"
              element={!setAccessCookie ? <Login setAccessCookie={setAccessCookie} /> : <Dashboard />}
            >
            </Route>
          </Routes>
        </BrowserRouter>
      </div>
    </div>
  );
}

export default App;
