import React from 'react';
import Axios from "axios"

export default function Dashboard() {

  Axios.get(process.env.REACT_APP_MAIN_DOMAIN + '/',{withCredentials: true}).then(function (response) {
    // handle success
    console.log(response);
  })
  .catch(function (error) {
    // handle error
    console.log(error);
  })

  return(
    <h2>Dashboard</h2>
  );
}