import React, { useState } from 'react';
import TextField from '@mui/material/TextField';
import InputAdornment from '@mui/material/InputAdornment';
import IconButton from '@mui/material/IconButton';
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';
import OutlinedInput from '@mui/material/OutlinedInput';
import InputLabel from '@mui/material/InputLabel';
import FormControl from '@mui/material/FormControl';
import Button from '@mui/material/Button';
import Container from '@mui/material/Button';
import '../App.css';
import PropTypes from 'prop-types';
import Axios from "axios"
import Cookies from "js-cookie"


export default function Register(props) {

  //MUI consts >>>>
  const [values, setValues] = React.useState({
    amount: '',
    email: '',
    password: '',
    weight: '',
    weightRange: '',
    showPassword: false,
  });

  const handleChange = (prop) => (event) => {
    setValues({ ...values, [prop]: event.target.value });
  };

  const handleClickShowPassword = () => {
    setValues({
      ...values,
      showPassword: !values.showPassword,
    });
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };
  //MUI consts <<<< 
  
  const handleSubmit = async e => {
    e.preventDefault();
    const accessCookie = await registerUser();
    console.log(accessCookie)
    props.setAccessCookie(accessCookie);
  }

  function registerUser(event) {
      // event.preventDefault();
      // console.log(values)
    return Axios.post(process.env.REACT_APP_MAIN_DOMAIN + '/register', {
      values
    },{withCredentials: true})
      .then(function (response) {
        console.log(response)
        return Cookies.get("access-token")
      })
  }

  return (
   
      <div className="login-wrapper">
        <h1>Please Register</h1>
        <form className="login-form" onSubmit={handleSubmit}>
          <div className="login-email-input">
            <TextField
              fullWidth
              required
              id="outlined-required"
              label="Email"
              placeholder="example@provider.com"
              onChange={handleChange('email')}
              value = {values.email}
            />
          </div>
          <div className="login-password-input">
            <FormControl variant="outlined" fullWidth>
              <InputLabel htmlFor="outlined-adornment-password">Password</InputLabel>
              <OutlinedInput
                id="outlined-adornment-password"
                required
                placeholder='choose a strong password'
                type={values.showPassword ? 'text' : 'password'}
                value={values.password}
                onChange={handleChange('password')}
                endAdornment={
                  <InputAdornment position="end">
                    <IconButton
                      aria-label="toggle password visibility"
                      onClick={handleClickShowPassword}
                      onMouseDown={handleMouseDownPassword}
                      edge="end"
                    >
                      {values.showPassword ? <VisibilityOff /> : <Visibility />}
                    </IconButton>
                  </InputAdornment>
                }
                label="Password"
              />
            </FormControl>
          </div>
          <div className="login-button">
            <Button variant="contained" type="submit" fullWidth size="large" margin="normal">Register</Button>
          </div>
        </form>
        <p onClick={() => {props.setLogin(true)}}>Already registered? Click here to log in</p>
  </div>
  )
}