import React, { useEffect, useState } from "react"
import { useParams } from "react-router-dom"
import Axios from "axios";
import Avatar from '@mui/material/Avatar';
import AvatarGroup from '@mui/material/AvatarGroup';
import TabsUnstyled from '@mui/base/TabsUnstyled';
import TabsListUnstyled from '@mui/base/TabsListUnstyled';
import TabPanelUnstyled from '@mui/base/TabPanelUnstyled';
import TabUnstyled from '@mui/base/TabUnstyled';

function GroupPage(props) {
    const { groupURL } = useParams();
    const [group, setGroup] = useState();
    const [membersId, setMembersId] = useState([]);
    const [members, setMembers] = useState([]); //full member profile
    const [chat, setChat] = useState([]); //existing chats in group item
    const [chatText, setChatText] = useState("") //new chats posted to group



    async function loadGroupData() {
        const groupData = await Axios.get(process.env.REACT_APP_MAIN_DOMAIN + '/group/' + groupURL)
            .then(function (response) {
                setGroup(response.data);
                setMembersId(response.data.members)
                return console.log("Data Loaded")
            })
    }

    async function loadMemberData() {
        // console.log(members)
        const memberData = await Axios.post(process.env.REACT_APP_MAIN_DOMAIN + '/group/members/fetch', membersId)
            .then(function (response) {
                setMembers(response.data)
            })
    }

    // async function loadGroupChats(){
    //  const groupChannel = group.channel
    //  const groupChats = await Axios.post(process.env.REACT_APP_MAIN_DOMAIN + '/chats/fetch', groupChannel)
    //  .then(function (response) {
    //      setChat(response.data)
    //  })
    // }

    useEffect(() => {
        loadGroupData()
    }, []);

    useEffect(() => {
        loadMemberData()
    }, [membersId]);

    // useEffect(()=>{
    //     loadGroupChats()
    // },[group])

    function joinGroup() {
        Axios.get(process.env.REACT_APP_MAIN_DOMAIN + '/group/' + groupURL + '/join-group', { withCredentials: true })
            .then(function (response) {
                console.log(response)
                loadGroupData()
            })
    }

    function leaveGroup() {
        Axios.get(process.env.REACT_APP_MAIN_DOMAIN + '/group/' + groupURL + '/leave-group', { withCredentials: true })
            .then(function (response) {
                console.log(response)
                loadGroupData()
            })
    }


    // function sendText(e) {
    //     e.preventDefault()
    //     // const chatPacket = {
    //     //     channel: group.channel,
    //     //     chatText: chatText
    //     // }
    //     Axios.post(process.env.REACT_APP_MAIN_DOMAIN + '/groups/'+ groupURL + '/chat', {chatText}, { withCredentials: true })
    //         .then(function (response) {
    //             console.log(response)
    //             loadGroupChats()
    //         })
    // }

    function getCurrentMember(id, resultKey) {
        const currentMember = members.find(m => m._id === id)
        if (!currentMember) {

        } else {
            return currentMember[resultKey]
        }
    }


    return (
        <div>
            <h1>Group Page</h1>
            <h2>{groupURL}</h2>
            <div className="group-page-wrapper">
                <div className="group-page-sidebar">
                    <p>Sidebar</p>
                    <div className="group-page-sidebar-element">Element</div>
                    <div className="group-page-sidebar-element">
                        <h3>Members</h3>
                        <AvatarGroup max={4}>
                            {members && members.map((member) => <Avatar key={member._id} alt={member.first_name} src={member.profile_pic} />)}
                        </AvatarGroup>
                    </div>
                </div>
                <div className="group-page-main">
                    <p>Main</p>
                    <TabsUnstyled defaultValue={0}>
                        <TabsListUnstyled>
                            <TabUnstyled>General</TabUnstyled>
                            <TabUnstyled>Members</TabUnstyled>
                            <TabUnstyled>Events</TabUnstyled>
                            <TabUnstyled>Chat</TabUnstyled>
                            <TabUnstyled>Forum</TabUnstyled>
                        </TabsListUnstyled>
                        <TabPanelUnstyled value={0}>General</TabPanelUnstyled>
                        <TabPanelUnstyled value={1}>Members</TabPanelUnstyled>
                        <TabPanelUnstyled value={2}>Events</TabPanelUnstyled>
                        <TabPanelUnstyled value={3}>Chat
                            {/* <div className="chats-wrapper">
                                <div>{chat && chat.map((c) =>
                                    <div key={c.index}>
                                    <div className="chat-header-wrapper">
                                        <Avatar className="chat-avatar" alt="null"
                                            src={members && getCurrentMember(c.sender_id, "profilePic")}></Avatar>
                                        <div className="chat-message-wrapper" >
                                        <p className="chat-sender-name">{members && getCurrentMember(c.sender_id, "firstName")}</p>
                                            <p className="chat-message">{c.content}</p>
                                        </div>
                                        <div className="chat-time-wrapper">
                                        <p className="chat-time">{new Date(c.created_date).toLocaleString()}</p>
                                        </div>
                                        </div>      
                                    </div>)}
                                </div>
                                <div className="chat-input-wrapper">
                                <form onSubmit={sendText}>
                                    <input className="chat-input" type="text"
                                        placeholder="write your message here"
                                        value={chatText}
                                        onChange={(e) => { setChatText(e.target.value); }}
                                    ></input>
                                    <button className ="chat-button" type="submit">Send</button>
                                </form>
                                </div>
                            </div> */}
                        </TabPanelUnstyled>
                        <TabPanelUnstyled value={4}>Forum</TabPanelUnstyled>
                    </TabsUnstyled>
                </div>
            </div>
            {members && membersId.map((memberId) => <p key={memberId}>{memberId}</p>)}
            <button onClick={joinGroup}>Join Group</button>
            <button onClick={leaveGroup}>Leave Group</button>
        </div>
    )
}

export default GroupPage