import React, { useState, useEffect } from "react";
import ContactsList from "./contactsList";
import ChatLog from "./chatLog"
import Box from '@mui/material/Box';
import Avatar from '@mui/material/Avatar';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import IconButton from '@mui/material/IconButton';
import Drawer from '@mui/material/Drawer';
import AvatarGroup from '@mui/material/AvatarGroup';





function Chats(props) {
    const currentMember = props.currentMember
    const isMobile = window.innerWidth <= 768;

    const [channel, setChannel] = useState();
    const [channelId, setChannelId] = useState([]);
    const [channelType, setChannelType] = useState();
    const [channelName, setChannelName] = useState();
    const [channelPic, setChannelPic] = useState();
    const [showChat, setShowChat] = useState(true);
    const [showContacts, setShowContacts] = useState(true);
    const [drawer, setDrawer] = useState(false)
    const [online, setOnline] = useState([]);
    // const [currentMember, setCurrentMember] = useState();

    useEffect(() => {
        if (isMobile) {
            setShowChat(false)
        }
    }, [])

    useEffect(() => {
        if (isMobile && channel) {
            // console.log(channel)
            setShowContacts(false)
            setShowChat(true)
        }
    }, [channel])

    function goBack() {
        setShowContacts(true)
        setShowChat(false)
    }

    function toggleDrawer(status) {
        setDrawer(status)
    }

    return (
        <div className="main-chats-wrapper">
            {/* <h1>Chats</h1> */}
            <div className="main-chats-body-wrapper">
                {showContacts && <ContactsList
                    setChannel={setChannel}
                    setChannelId={setChannelId}
                    setChannelType={setChannelType}
                    setChannelName={setChannelName}
                    setChannelPic={setChannelPic}
                    currentMember={currentMember}
                // setCurrentMember={props.setCurrentMember}    
                />}
                {showChat && <div className="chat-log-wrapper" >
                    <div className="chat-header">
                        <Box sx={{ boxShadow: 5, height: "100%", padding: "30px", display: "flex", flexDirection: "row", alignItems: "center" }}>
                            {isMobile &&
                                <IconButton onClick={goBack}>
                                    <ArrowBackIcon />
                                </IconButton>
                            }
                            {channelPic && <Avatar sx={{ margin: "10px" }} alt={channelName} src={channelPic} />}
                            {channelName}
                            <IconButton sx={{ marginLeft: "auto" }} onClick={() => { setDrawer(true) }}>
                                <ExpandMoreIcon/>
                            </IconButton>
                        </Box>
                    </div>
                    {channel ? <ChatLog
                        channel={channel}
                        channelId={channelId}
                        channelType={channelType}
                        currentMember={currentMember}
                        online={online}
                        setOnline={setOnline}
                    />
                        :
                        <div className="chats-wrapper">
                            <p>Select a channel to begin chatting!</p>
                        </div>
                    }
                </div>}
                <Drawer
                    anchor={'bottom'}
                    open={drawer}
                    onClose={() => { toggleDrawer(false) }}
                >
                <div className="chat-info-drawer">
                    This ia a drawer
                    <AvatarGroup max={4}>
                            {online && online.map((member) => <Avatar key={member._id} alt={member.first_name} src={member.profile_pic} />)}
                        </AvatarGroup>
                    </div>
                </Drawer>
            </div>
        </div>
    )
}

export default Chats